.footer__item:nth-child(2) svg {
  width: 16px;
  height: 16px; }
  @media (min-width: 1366px) {
    .footer__item:nth-child(2) svg {
      width: 20px;
      height: 20px; } }

.footer__item:nth-child(3) svg {
  width: 16px;
  height: 13px; }
  @media (min-width: 1366px) {
    .footer__item:nth-child(3) svg {
      width: 19px;
      height: 16px; } }

.logo {
  display: flex;
  width: 70px;
  height: 38px; }

.logo svg {
  fill: #f9fbfd;
  width: 70px;
  height: 38px; }

@media (max-width: 768px) {
  [data-logo="is-open"] svg path {
    fill: #011c40;
    width: 42px;
    height: 22px; } }

.logo svg path {
  fill: #f9fbfd; }
  @media (min-width: 768px) {
    .logo svg path {
      fill: #f9fbfd; } }

